.spinner {
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: block;
  width: 75px;
  height: 75px;
  border-radius: 50%; }
  .spinner:before, .spinner:after {
    position: absolute;
    content: '';
    display: block;
    height: 100%;
    width: 100%;
    border-radius: 50%;
    border: 5px solid #eee; }
  .spinner:before {
    background-color: #ffd700;
    border: 0;
    transform: scale(-0.3);
    animation: spinnerB 1s linear infinite; }
  .spinner:after {
    bottom: 0;
    left: 0;
    border-bottom-color: #ffd700;
    animation: spinner 0.5s linear infinite; }

@keyframes spinner {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@keyframes spinnerB {
  0% {
    transform: scale(-0.3);
    opacity: 1; }
  70% {
    opacity: 0;
    transform: scale(0.75); }
  100% {
    transform: scale(-0.3); } }

